import Vue from 'vue'
import App from './App.vue'
import './plugins/element.js'
import ElementUI from 'element-ui';
import {
  Message
} from 'element-ui'

Vue.config.productionTip = false
Vue.use(ElementUI)
Vue.prototype.$message = Message

new Vue({
  render: h => h(App),
}).$mount('#app')
