        :rules="addformRules"
        ref="addFromRef"
      >
        <el-form-item label="姓名" prop="name">
          <el-input
            style="width: 300px"
            v-model="addform.name"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="年龄" prop="age">
          <el-input
            style="width: 300px"
            type="number"
            v-model="addform.age"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="sex">
          <el-select style="width: 300px" v-model="addform.sex">
            <el-option label="男" value="男"></el-option>
            <el-option label="女" value="女"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="联系电话" prop="phone">
          <el-input
            style="width: 300px"
            v-model="addform.phone"
            type="number"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="详细地址" prop="address">
          <el-cascader
            style="width: 300px"
            placeholder="请点击选择地址"
            :options="options"
            v-model="qyzcd"
          ></el-cascader>
          <el-input
            style="width: 300px"
            v-model="addform.address"
            autocomplete="off"
            placeholder="请输入详细地址"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addForm">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 编辑对话框 -->
    <el-dialog
      center
      title="新建/编辑用户"
      width="500px"
      :visible.sync="editformVisible"
    >
      <el-form
        :model="editform"
        label-width="100px"
        :rules="editformRules"
        ref="editFromRef"
      >
        <el-form-item label="姓名" prop="name">
          <el-input
            style="width: 300px"
            v-model="editform.name"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="年龄" prop="age">
          <el-input
            style="width: 300px"
            type="number"
            v-model="editform.age"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="sex">
          <el-select style="width: 300px" v-model="editform.sex">
            <el-option label="男" value="男"></el-option>
            <el-option label="女" value="女"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="联系电话" prop="phone">
          <el-input
            style="width: 300px"
            v-model="editform.phone"
            type="number"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="详细地址" prop="address">
          <el-cascader
            style="width: 300px"
            placeholder="请点击选择地址"
            :options="options"
            v-model="qyzcd"
          ></el-cascader>
          <el-input
            style="width: 300px"
            v-model="editform.address"
            autocomplete="off"
            placeholder="请输入详细地址"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editformVisible = false">取 消</el-button>
        <el-button type="primary" @click="editForm">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    const checkMobile = (rule, value, cb) => {
      let reg = /^1[3-9]\d{9}$/;
      if (reg.test(value)) return cb();
      cb(new Error("请输入合法的手机"));
    };
    return {
      dialogFormVisible: false,
      editformVisible: false,
      form: [
        {
          id: 0,
          name: "张三",
          age: "21",
          sex: "男",
          phone: "12345678912",
          address: "广东省广州市白云区",
        },
        {
          id: 1,
          name: "李四",
          age: "21",
          sex: "女",
          phone: "12345678913",
          address: "广东省广州市白云区",
        },
        {
          id: 2,
          name: "王五",
          age: "22",
          sex: "男",
          phone: "12345678914",
          address: "广东省广州市白云区",
        },
        {
          id: 3,
          name: "王五",
          age: "22",
          sex: "男",
          phone: "12345678914",
          address: "广东省广州市白云区",
        },
      ],
      options: [
        {
          value: "广东省",
          label: "广东省",
          children: [
            {
              value: "广州市",
              label: "广州市",
              children: [
                {
                  value: "荔湾区",
                  label: "荔湾区",
                },
                {
                  value: "越秀区",
                  label: "越秀区",
                },
                {
                  value: "海珠区",
                  label: "海珠区",
                },
                {
                  value: "天河区",
                  label: "天河区",
                },
                {
                  value: "白云区",
                  label: "白云区",
                },
                {
                  value: "黄埔区",
                  label: "黄埔区",
                },
                {
                  value: "番禺区",
                  label: "番禺区",
                },
                {
                  value: "花都区",
                  label: "花都区",
                },
                {
                  value: "南沙区",
                  label: "南沙区",
                },
                {
                  value: "从化区",
                  label: "从化区",
                },
                {
                  value: "增城区",
                  label: "增城区",
                },
              ],
            },
          ],
        },
      ],
      addformRules: {
        name: [{ required: true, message: "请输入名字", trigger: "blur" }],
        age: [{ required: true, message: "请输入年龄", trigger: "blur" }],
        sex: [
          {
            required: true,
            message: "请输入性别",
            trigger: "blur",
          },
        ],
        phone: [
          {
            required: true,
            message: "请输入电话号码",
            trigger: "blur",
          },
          {
            validator: checkMobile,
            trigger: "blur",
          },
        ],
        address: [
          {
            required: true,
            message: "请输入地址",
            trigger: "blur",
          },
        ],
      },
      addform: {
        name: "",
        age: "",
        sex: "",
        phone: "",
        address: "",
      },
      editform: {},
      editformRules: {
        name: [{ required: true, message: "请输入名字", trigger: "blur" }],
        age: [{ required: true, message: "请输入年龄", trigger: "blur" }],
        sex: [
          {
            required: true,
            message: "请输入性别",
            trigger: "blur",
          },
        ],
        phone: [
          {
            required: true,
            message: "请输入电话号码",
            trigger: "blur",
          },
          {
            validator: checkMobile,
            trigger: "blur",
          },
        ],
        address: [
          {
            required: true,
            message: "请输入地址",
            trigger: "blur",
          },
        ],
      },
      input: "",
      changeID: [],
      qyzcd: "",
    };
  },
  computed: {
    formall() {
      let arr = [];
      this.form.forEach((vaild) => {
        if (vaild.name.indexOf(this.input) > -1) {
          arr.push(vaild);
        }
      });
      return arr;
    },
  },
  methods: {
    handleSelectionChange(val) {
      let arr = [];
      val.forEach((vaild) => {
        arr.push(vaild.id);
      });
      this.changeID = arr;
      console.log(this.changeID);
    },
    deleteMore() {
      this.changeID.forEach((vaild) => {
        this.form.splice(
          this.form.findIndex((vaild1) => {
            console.log(vaild);
            return vaild1.id === vaild;
          }),
          1
        );
      });
    },
    deleteUser(e) {
      console.log(e);
      this.form.splice(
        this.form.findIndex((vaild) => {
          return vaild.id === e;
        }),
        1
      );
      // console.log(this.form)
    },
    addForm() {
      let address = this.qyzcd.join("");
      this.addform.address = address + this.addform.address;
      this.addform.id = this.form.length;
      console.log(this.addform.address);
      this.$refs.addFromRef.validate((valid) => {
        if (!valid) return this.$message.error("添加用户失败");
        this.form.push(this.addform);
        this.dialogFormVisible = false;

        this.$message.success("添加成功");
      });
    },
    editVisible(e) {
      console.log(e);
      let index = this.form.findIndex((vaild) => {
        return vaild.id === e;
      });
      this.editform = this.form[index];
      console.log(this.form);
      this.editformVisible = true;
    },
    editForm() {
      // let index  =
      // console.log(index);
      this.$refs.editFromRef.validate((valid) => {
        if (!valid) return this.$message.error("修改用户失败");
        let address = this.qyzcd.join("");
        this.editform.address = address + this.editform.address;
        this.form.findIndex((vaild) => {
          return vaild.id === this.editform.id;
        });
        this.editformVisible = false;
        this.editform = [];
        this.$message.success("修改成功");
      });
    },
  },
};
</script>

